import Link from 'next/link'

const linkStyle = {
  marginRight: 15
}

const contentM = {
  float: 'left',
  width: '260px',
  display: 'list-item',
  backgroundColor: 'red'
}

const Sidebar = () => (
    <div style={contentM}>
      <h1>Menu</h1>
      <Link href="/">
        <a style={linkStyle}>Truc</a>
      </Link>
      <Link href="/">
        <a style={linkStyle}>Truc2</a>
      </Link>
    </div>
)

export default Sidebar



